@font-face {
  font-family: "Manrope";
  src: url('../fonts/Manrope-Regular.ttf');

}

@font-face {
  font-family: "Lucida Console";
  src: url('../fonts/Lucida.ttf');

}

@font-face {
  font-family: "Open Sans";
  src: url('../fonts/OpenSans/OpenSans-Regular.ttf');

}

@font-face {
  font-family: "Source Sans Pro";
  src: url('../fonts/SourceSans/SourceSansPro-Regular.ttf');

}

* {
  margin: 0;
  padding: 0;
  outline: none;
}

#root {
  overflow-x: hidden;
  height: 100%;
}

body {
  font-family: Manrope;
}

a {
  text-decoration: none;
}

.navigation-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0;
  padding-top: 30px;
  padding-bottom: 30px;

  a {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #282828;
  }


  .order {
    padding: 14px 58px;
    background: #2787F4;
    border-radius: 7px;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
  }

  .burger-menu {
    display: none;
  }

}

.active {
  border-bottom: 1px solid black;
}

.no-scrolling {
  position: fixed;
  overflow: hidden;
  height: 100%;
}

.mobile-menu {
  display: none;
  z-index: 100;
  background-color: #fff;
  position: fixed;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  animation-duration: 1000ms;
  overflow: hidden;


  a {
    color: #0d0d0d;
    font-size: 24px;
    margin-top: 20px;
  }

}

.mobile-menu-open {
  animation-name: open;

}

@keyframes open {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}


.main {
  &-desc {
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    margin-bottom: 150px;

    .item {
      width: 100%;
      display: flex;
      justify-content: center;
    }

    .left {
      img {
        margin-right: 33px;
        margin-left: 33px;
      }

    }

    .right {
      font-style: normal;
      font-weight: normal;
      font-size: 26px;
      line-height: 36px;
      color: #000000;
      width: 90%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h1 {
        margin-top: 70px;
        margin-bottom: 20px;
        font-family: "Lucida Console";
        text-transform: uppercase;
        font-size: 116px;
        line-height: 0.8;
        font-weight: 400;
        letter-spacing: -0.07em;
        color: #000000;


      }

      p {
        width: 90%;
        font-style: normal;
        font-weight: 500;
        font-size: 26px;
        color: #0D0D0D;

      }
    }

  }

  &-content {
    padding-top: 20px;
    background-color: #000;
    background-size: cover;
    width: 100%;
    color: white;

    p {
      font-family: Manrope;
      font-style: normal;
      font-weight: 300;
      font-size: 24px;
      line-height: 33px;

    }

    h2 {
      font-family: Lucida Console;
      font-style: normal;
      font-weight: normal;
      font-size: 46px;
      line-height: 46px;
      margin-bottom: 20px;
    }


    &-container {
      margin-top: 50px;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      .creative {
        font-family: Lucida Console;
        font-size: 96px;
        line-height: 96px;
        letter-spacing: 0.18em;
        color: rgba(255, 255, 255, 0.2);
        transform: rotate(180deg);
        //transform-origin: left top 0;
        writing-mode: tb-rl;
        margin-left: 20px;
        margin-right: 20px;

      }

      .desc-container {
        display: flex;
        align-items: center;
        justify-content: center;

        .img {
          height: 489px;
          width: 480px;
          background: #F1F1F1;
          border-radius: 10px;

        }

        .desc-right {
          color: #FFFFFF;
          width: 100%;
          margin-left: 40px;
          text-align: right;
          margin-right: 100px;


        }

        .desc-left {
          margin: 0;
          text-align: left;
        }
      }
    }
  }

  .left {


    .creative-left {
      transform: none;

    }

    .desc-left {
      margin-right: 40px;
      margin-left: 100px;
    }
  }

}

.next-page {
  color: #fff;
  width: 100%;
  margin-top: 80px;
  padding-bottom: 50px;
  margin-left: calc(100% - 94px);

  span {
    margin-left: 20px;
  }
}

.navigation-pages {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 100px;

  .next-page {
    margin: 0;
    padding: 0;


    span {
      position: absolute;

    }
  }

  .go-back {
    position: relative;

    span {
      position: absolute;
      right: 0px;
    }
  }

}

.services {
  display: flex;
  align-items: center;
  justify-content: space-around;

  &-title {
    display: flex;


    h1 {
      font-style: normal;
      text-align: center;
      font-weight: 600;
      font-size: 46px;
      line-height: 112.7%;
      letter-spacing: 0.02em;
      color: #0D0D0D;
      margin: 0 auto;

      span {
        border-bottom: 1px solid black;
      }

    }
  }

  &-item {
    list-style: none;
    width: 45%;


    .items-title {
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 33px;
      color: #0D0D0D;

      span {
        margin-right: 20px;
      }
    }

    .items-desc {
      font-style: normal;
      font-weight: 300;
      font-size: 22px;
      line-height: 30px;
      color: #0D0D0D;
      width: 70%;
      margin-left: 50px;
      margin-top: 34px;
      margin-bottom: 30px;
    }

    .items-price {
      display: block;
      font-weight: bold;
    }
  }
}

.projects {
  padding-top: 200px;
  background-image: url('../../assets/img/projectsBg.png');
  background-size: cover;
  background-repeat: no-repeat;

  &-items {
    padding-bottom: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    text-align: center;
    color: #FFFFFF;

    .item {
      margin-right: 20px;
    }
  }

}

.footer {
  padding-top: 94px;
  margin-bottom: 30px;
  display: flex;
  align-items: flex-end;
  justify-content: space-around;

  &-btn {
    background: #2787F4;
    border-radius: 7px;
    padding: 18px 32px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    letter-spacing: 0.1em;
  }

  &-amstudio {
    font-family: Lucida Console;
    font-style: normal;
    font-weight: normal;
    font-size: 120px;
    letter-spacing: -0.06em;
    color: #000000;
    text-transform: uppercase;

    span {
      -webkit-text-stroke: 2px black;
      color: white;
      text-shadow: 0 0 2px #000;
    }
  }

}

.about-wrapper {
  background: #0D0D0D;
  color: #fff;

  &-title {
    margin: 0 auto;
    padding-top: 100px;
    width: 50%;
    text-align: center;
    font-style: normal;
    font-weight: 600;
    font-size: 27px;
    line-height: 112.7%;
    letter-spacing: 0.02em;

    color: #FFFFFF;


  }

  &-icons {
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
    padding-bottom: 167px;

    .person {
      text-align: center;
      font-weight: 500;
      font-size: 24px;
      line-height: 33px;

    }
  }

  &-work {
    width: 90%;
    margin: 0 auto;

    .title {
      font-style: normal;
      font-weight: 600;
      font-size: 46px;
      line-height: 112.7%;
      text-align: center;
      letter-spacing: 0.02em;
      margin-bottom: 80px;
    }

    .work-items {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      font-style: normal;
      font-weight: normal;
      font-size: 35px;
      line-height: 109.7%;
      margin-bottom: 80px;

      .work-item {
        display: flex;
        justify-content: flex-start;
        width: 30%;

        span {
          margin-right: 10px;
          font-style: normal;
          font-weight: normal;
          font-size: 28px;
          line-height: 109.7%;
          color: #FFFFFF;
          opacity: 0.8;
        }
      }

    }
  }
}

.social-icons {
  margin-top: 20px;

  a:nth-child(2) {
    margin: 10px 31px;
  }
}

.contacts {
  background: #000;
  width: 100%;
  padding-top: 66px;

  &-form {
    width: 90%;
    background: #EFEFEF;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;

    .white-header {
      background: #fff;
      width: 100%;
      height: 68px;
    }

    .cont {
      margin-top: 19px;
      display: flex;
      flex-direction: column;
      align-items: center;

      h2 {
        font-style: normal;
        font-weight: 600;
        font-size: 46px;
        line-height: 112.7%;
        text-align: center;
        letter-spacing: 0.02em;
        color: #0D0D0D;
        margin-bottom: 31px;
      }

      p {
        font-style: normal;
        font-weight: 500;
        font-size: 27px;
        line-height: 37px;
        text-align: center;
        color: #0D0D0D;
        margin-bottom: 7px;
      }

    }

    .form {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 60%;
      background: #FFFFFF;
      border-radius: 7px;
      margin: 0 auto;
      margin-bottom: 73px;
      margin-top: 58px;
      border-radius: 7px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

      label {
        padding-top: 26px;
        display: block;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 23px;
        color: #323333;
        margin-bottom: 11px;
      }

      input {
        display: block;
        border: none;
        background: rgba(230, 230, 230, 0.34);
        border-radius: 7px;
        width: 80%;
        height: 57px;
        font-size: 20px;
        text-align: center;
        color: black;
        font-family: "Source Sans Pro";
      }

      ::-webkit-input-placeholder {
        color: #949494;
        font-size: 16px;

      }

      .errors {
        color: red;

      }

      .errorsInput {
        border: 1px solid red
      }

      textarea {
        padding-top: 20px;
        font-family: "Source Sans Pro";
        width: 80%;
        background: rgba(230, 230, 230, 0.34);
        text-align: center;
        border: none;
        resize: none;
        font-size: 20px;
      }

      .send-btn {
        display: flex;
        align-items: center;
        color: #fff;
        font-style: normal;
        font-weight: 600;
        font-size: 21px;
        line-height: 29px;
        padding: 19px 56px;
        background: #2787F4;
        border-radius: 7px;
        margin-bottom: 37px;
        cursor: pointer;
        margin-top: 30px;

        &-title {
          margin-right: 47px;
        }
      }

      .rules {
        font-style: normal;
        font-weight: 300;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: -0.01em;
        color: #323333;
        margin-bottom: 33px;
        font-family: "Open Sans";
      }
    }
  }

  .navigation-pages {
    padding-top: 50px;
    padding-bottom: 60px;
  }
}

#root .modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  z-index: 1000;

  &-container {
    width: 30%;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 450px;
    max-width: 700px;
    background: #F9F9F9;

    .modal_header {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      font-weight: 600;
      font-size: 18px;
      line-height: 23px;
      color: #444444;
      padding: 16px 20px;
      border-bottom: 2px solid #D2D6DE;
    }

    .modal_content {
      display: flex;
      justify-content: space-between;
      padding: 32px 20px 40px 20px;
      border-bottom: 2px solid #D2D6DE;
    }

    .modal_footer {
      display: flex;
      justify-content: flex-end;
      padding: 24px 20px;

      .modal_footer_btn {
        text-align: center;
        padding: 7.5px 20px;
        border-radius: 3px;
        font-weight: normal;
        font-size: 14px;
        line-height: 130%;
        outline: none;
        cursor: pointer;
        text-decoration: none;
      }

      .doneBtn {
        background: #2787F4;
        font-weight: 600;
        color: #fff;
        border: none;
      }
    }

  }
}

.error-wrapper {
  font-size: 14px;
  line-height: 18px;
  color: #dc3545;
  padding: 5px;
  min-height: 28px;
  margin-bottom: -28px;
}

